/**
 * A Web Component to get currency displaying correctly in Recharge
 * Usage: <localized-price data-price="{{ subscription.price }}" data-currency="{{ subscription.presentment_currency }}"></localized-price>
 */
export default class LocalizedPrice extends HTMLElement {
  constructor() {
    super();

    if (!this.currency || !this.price) return;

    this.innerHTML = this.localizedPrice;
  }

  get currency() {
    return this.dataset.currency ?? "USD";
  }

  get price() {
    return Number(this.dataset.price);
  }

  get localizedPrice() {
    return new Intl.NumberFormat(undefined, {
      style: "currency",
      currency: this.currency,
    }).format(this.price);
  }
}
